import styled, { css } from "styled-components";

export const SIDE_PADDING = "16px";

const ARTICLE_CONTENT_WIDTH = "920px";
export const ARTICLE_WIDTH = `calc(${ARTICLE_CONTENT_WIDTH} + 2 * ${SIDE_PADDING})`;
export const BOX_WIDTH = `calc(${ARTICLE_WIDTH} + 2 * ${SIDE_PADDING})`;

// Same width with article box, but has paddng under max width
export const LikeBoxCss = css`
  width: 100%;

  padding: 0 ${SIDE_PADDING};

  @media (min-width: ${ARTICLE_WIDTH}) {
    padding: 0;

    max-width: ${BOX_WIDTH};
  }
`;

// Used for article box, additional box padding beyong max width
export const BoxCss = css`
  width: 100%;

  padding: 20px 0;

  @media (min-width: ${ARTICLE_WIDTH}) {
    padding: 40px ${SIDE_PADDING};

    max-width: ${BOX_WIDTH};
  }
`;

// Limited article width with padding, used for most of article components
export const WrapperCss = css`
  padding: 0 ${SIDE_PADDING};

  max-width: ${ARTICLE_WIDTH};
`;

// Same as above but removes padding once under max width
const ExtendingCss = css`
  width: 100%;
  padding: 0;

  @media (min-width: ${ARTICLE_WIDTH}) {
    padding: 0 ${SIDE_PADDING};

    max-width: ${ARTICLE_WIDTH};
  }
`;

// Utility components
export const ExtendingWrapper = styled.div`
  ${ExtendingCss}
`;

export const WidthWrapper = styled.div`
  ${WrapperCss}
`;

export const BoxWrapper = styled.div`
  ${BoxCss}
`;

export const LikeBoxWrapper = styled.div`
  ${LikeBoxCss}
`;
